svg {
  flex-shrink: 0;
}

.load-data {
  display: block;
  margin: 6px auto;
}

.red-hole {
  stroke-width: 0;
  r: 36px;
  cy: 99px;
  fill: #fe2c55;
  cx: 128px;
  -webkit-animation: redHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
  animation: redHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
  fill: #000;
}

.green-hole {
  stroke-width: 0;
  r: 36px;
  cy: 99px;
  fill: #3af2ff;
  cx: 60px;
  -webkit-animation: greenHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
  animation: greenHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
  fill: #000;
}

.circle-1 {
  stroke-width: 0;
  r: 36px;
  cy: 99px;
  fill: #0f0f0f;
  cx: 60px;
  -webkit-animation: greenHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
  animation: greenHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
}

.circle-2 {
  stroke-width: 0;
  r: 36px;
  cy: 99px;
  fill: #3af2ff;
  cx: 60px;
  -webkit-animation: greenHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
  animation: greenHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
}

.circle-3 {
  stroke-width: 0;
  r: 36px;
  cy: 99px;
  fill: #fe2c55;
  cx: 128px;
  -webkit-animation: redHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
  animation: redHoleLoading 1s cubic-bezier(0.05, 0, 1, 1) infinite;
}

@keyframes redHoleLoading {
  25% {
    cx: 100px;
    r: 24px;
  }
  50% {
    cx: 60px;
    r: 36px;
  }
  75% {
    cx: 100px;
    r: 43.199999999999996px;
  }
  100% {
    cx: 140px;
    r: 36px;
  }
}

@keyframes greenHoleLoading {
  25% {
    cx: 100px;
    r: 43.199999999999996px;
  }
  50% {
    cx: 140px;
    r: 36px;
  }
  75% {
    cx: 100px;
    r: 21.599999999999998px;
  }
  100% {
    cx: 60px;
    r: 36px;
  }
}
